'use client';

import React from 'react';
import { useRouter, usePathname } from 'next/navigation';
import { useTranslation } from 'react-i18next';
import { useNotifications } from '../hooks/useNotifications';

const BottomNav: React.FC = () => {
  const router = useRouter();
  const pathname = usePathname();
  const { t } = useTranslation();
  const { unreadCount } = useNotifications();

  const isActive = (path: string) => pathname.includes(path);

  const navItems = [
    { name: t('nav.matches'), icon: 'sports_soccer', path: '/matches' },
    { name: t('nav.history'), icon: 'history', path: '/history' },
    { name: t('nav.standings'), icon: 'leaderboard', path: '/standings' },
    { name: t('nav.notifications'), icon: 'notifications', path: '/notifications', showBadge: true },
    { name: t('nav.profile'), icon: 'person', path: '/profile' },
  ];

  return (
    <div className="fixed bottom-0 left-0 w-full h-20 bg-white/95 backdrop-blur-lg border-t border-slate-200 flex items-center justify-between md:justify-center md:gap-16 px-6 pb-4 pt-2 z-50 shadow-[0_-4px_6px_-1px_rgba(0,0,0,0.05)]">
      {navItems.map((item) => (
        <button
          key={item.path}
          onClick={() => router.push(item.path)}
          className={`flex flex-col items-center gap-1 transition-colors min-w-[48px] ${isActive(item.path) ? 'text-primary' : 'text-slate-400 hover:text-slate-600'
            }`}
        >
          <div className="relative">
            <span className={`material-symbols-outlined text-2xl ${isActive(item.path) ? 'icon-filled' : ''}`}>
              {item.icon}
            </span>
            {item.showBadge && unreadCount > 0 && (
              <span className="absolute -top-1 -right-1 flex h-4 min-w-[16px] px-1 items-center justify-center rounded-full bg-red-500 text-[10px] font-bold text-white border-2 border-white">
                {unreadCount > 9 ? '9+' : unreadCount}
              </span>
            )}
          </div>
          <span className={`text-[10px] ${isActive(item.path) ? 'font-bold' : 'font-medium'}`}>
            {item.name}
          </span>
        </button>
      ))}
    </div>
  );
};

export default BottomNav;